body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin-top: 30px;
}

.header {
  margin-top: 15px;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #333;
}

.loading {
  text-align: center;
  margin-bottom: 30px;
}

.exchange-value {
  text-align: right;
}

.forex-column-right {
  display: flex;
  justify-content: center;
  border-left: 1px solid #333;
  padding-left: 0;
}

.btn-delete-currency {
  align-self: center;
  font-size: 1.5rem;
}

.forex-card {
  margin-bottom: 15px;
}

.forex-add-currency {
  margin-bottom: 120px;
}

